@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  scroll-behavior: smooth;
}

.navbar {
  display: flex;
  justify-content: center;
  gap: 1.5rem;
  background-color: #333;
  padding: 1rem;
}

.navbar a {
  color: white;
  text-decoration: none;
  font-weight: bold;
}

.section {
  padding: 3rem 1rem;
  max-width: 900px;
  margin: auto;
}

.intro {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.intro img {
  width: 150px;
  border-radius: 50%;
  margin-bottom: 1rem;
}

.skills {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  list-style: none;
  padding: 0;
}

.skills li {
  background: #eee;
  padding: 0.5rem 1rem;
  border-radius: 8px;
}

.timeline {
  list-style: none;
  padding: 0;
  position: relative;
}

.timeline li {
  margin: 2rem 0;
  display: flex;
  align-items: flex-start;
  position: relative;
}

.timeline .dot {
  width: 12px;
  height: 12px;
  background: #000;
  border-radius: 50%;
  margin-right: 1rem;
  margin-top: 0.4rem;
}

.cards {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.card {
  display: flex;
  gap: 1rem;
  background: #f9f9f9;
  padding: 1rem;
  border-radius: 8px;
}

.card img {
  width: 120px;
  height: auto;
  object-fit: cover;
  border-radius: 8px;
}

.card-text {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

@media (min-width: 768px) {
  .intro {
    flex-direction: row;
    text-align: left;
    gap: 2rem;
  }

  .cards {
    flex-direction: row;
    flex-wrap: wrap;
  }

  .card {
    width: calc(50% - 1rem);
  }
}